
.card {
  border: 1px solid #f3f3f3;
  padding: 30px;
  width: 220px;
  border-radius: 15px;
  margin-right: 30px;
  margin-bottom: 40px;
  transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
  &:hover{
    box-shadow: 0 20px 30px rgba(0,0,0,0.1);
    transform: translateY(-5px);
  }
  .favorite {
    position: absolute;
    cursor: pointer;
  }
  span{
    font-size: 13px;
    opacity: 0.3;
    text-transform: uppercase;
  }
  b{
    font-size: 14px;
  }
  h5 {
    font-weight: 400;
    font-size: 14px;
  }
  .plus{
    cursor: pointer;
  }
}