
 body {
  margin: 0;
  font-family: system-ui;
  background-color: #e7f6ff;
  height: 100%;
}


.wrapper{
  background:#ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.1);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}

header{
  border-bottom: 1px solid #eaeaea;

  img {
    margin-right: 15px;
  }
  h3,p{
    margin: 0;
  }
}

.content {
  h1{
    margin: 0;
  }
}

 .cartEmpty {
   text-align: center;

   p {
     width: 280px;
     line-height: 24px;
   }

   .greenButton {
     width: 245px;
     margin-top: 20px;

     &:hover {
       img {
         transform: rotate(180deg) translateX(3px);
       }
     }

     img {
       position: relative;
       top: 1px;
       transform: rotate(180deg);
       margin-right: 15px;
       transition: transform 0.15s ease-in-out;
     }
   }
 }

.button {
  background: #ffffff;
  border: 1px solid #f2f2f2;
  box-sizing: border-box;
  border-radius: 8px;
  height: 32px;
  width: 32px;
  cursor: pointer;
}



.search-block{
  border: 1px solid #f2f2f2;
  border-radius: 10px;
  padding: 0 15px;
  position: relative;

  .clear {
    position: absolute;
    right: 15px;
    width: 18px;
    height: 18px;
    top: 14px;
  }

  input{
    border: 0;
    cursor: pointer;
    padding: 13px;
    font-size: 16px;
    width: 300px;
  }
}

.greenButton {
  width: 245px;
  margin-top: 20px;
  &:hover{
    img {
      transform: rotate(180deg) translateX(3px);
    }
  }
  img {
    position: relative;
    top: 1px;
    transform: rotate(180deg);
    margin-right: 15px;
    transition: transform 0.15s ease-in-out;
  }
}





.cartTotalBlock{
 ul {
   display: block;
   margin-bottom: 40px !important;

   li{
     display: flex;
     align-items: flex-end;
     margin-bottom: 20px;
     div{
       flex: 1;
       height: 1px;
       border-bottom: 1px dashed  #dfdfdf;
       position: relative;
       top: -3px;
       margin: 0 5px;
     }
   }

 }
  .greenButton{
    position: relative;

    &:disabled  {
      animation: button-loading 0.7s ease-in-out infinite;
    }

    &:hover{
      img{
        transform: translateX(5px);
      }
    }
    img{
      position: absolute;
      right: 30px;
      top: 20px;
      transition: transform 0.2s ease-in-out;
    }
  }
}
.greenButton{
    width: 100%;
    border-radius: 18px;
    height: 55px;
    background: #9DD558;
    border: 0;
    color: #ffffff;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.2s ease-in-out;

  &:disabled  {
    background-color: #bebebe !important;
    cursor: default;
  }
    &:hover{
      background: lighten( #9DD558, 5%);
    }
    &:active{
      background: darken( #9DD558, 20%);
    }
  }



.cartItem {
  border: 1px solid #f3f3f3;
  border-radius: 15px;
  overflow: hidden;
  padding: 10px;
  .cartItemImg{
    width: 70px;
    height: 70px;
    background-size: contain;
    background-position: 0 -2px;
    background-repeat: no-repeat;
    margin-right: 10px;
  }
  p {
    font-size: 16px;
    margin: 0;
  }
  b{
    font-size: 14px;
  }
  .removeBth{
    cursor: pointer;
    opacity: 0.35;
    transition: opacity 0.2s ease-in-out;
    &:hover{
      opacity: 1;
    }
  }
}
 @keyframes button-loading {
   0% {
     opacity: 1;
   }
   50% {
     opacity: 0.7;
   }
   100% {
     opacity: 1;
   }
 }